import React from 'react';
import {useParams} from "react-router-dom"
import InpatientReportsByOrganizationsForm066Container
    from "../containers/InpatientReportsByOrganizationsForm066Container";

const InpatientReportsByOrganizationsForm066Page = () => {
    const {year, month,group,form} = useParams()
    return (
        <>
            <InpatientReportsByOrganizationsForm066Container year={year} month={month} group={group} form={form}/>
        </>
    );
};

export default InpatientReportsByOrganizationsForm066Page;